.garden {
    width: 100%;
    height: 100%;
    position: relative;
}

.garden ul.cards {
    width: 100%;
    height: 95%;
    box-sizing: border-box;
    background-color: rgb(181, 230, 29);
    overflow-x: hidden;
    overflow-y: scroll;
}

.garden ul.cards .addNewPlant {
    width: 290px;
    height: 200px;
    float: left;
    margin: 7px;
    overflow: hidden;
}

.garden ul.cards .addNewPlant img {
    box-shadow: 0px 0px 15px #666666;
    border-radius: 50%;
    transition-duration: 100ms;
    transition-property: all;
    transition-timing-function: ease-in-out;
    margin-top: 50px;
    margin-left: 95px;
}

.garden ul.cards .addNewPlant img:hover {
    transform: rotate(90deg);
}

.garden ul.cards .addNewPlant img:active {
    box-shadow: none;
}