.playground {
    width: 100vw;
    height: 100vh;
    min-width: 1536px;
    min-height: 754.4px;
}

.playground .leftPart {
    width: 20%;
    height: 100%;
    background-color: #e6b800;
    float: left;
    position: relative;
}

.playground .rightPart {
    width: 80%;
    height: 100%;
    float: left;
}

.playground .leftPart .nav_btn {
    border-style: none;
    width: 90%;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 5%;
    font-size: 30px;
    text-align: center;
    line-height: 40px;
    outline: none;
    border-radius: 10px;
    font-weight: bold;
    box-sizing: border-box;
    border: 2px solid #ff6933;
}